import { Tooltip } from 'nextcloud-vue';
export default {
  name: 'stun-server',
  props: {
    server: {
      type: String,
      default: '',
      required: true
    },
    index: {
      type: Number,
      default: -1,
      required: true
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  directives: {
    tooltip: Tooltip
  },
  methods: {
    removeServer(event) {
      this.$emit('removeServer', this.index);
    },

    update(event) {
      this.$emit('update:server', event.target.value);
    }

  }
};