import { Tooltip } from 'nextcloud-vue';
import debounce from 'debounce';
import StunServer from './components/StunServer';
export default {
  name: 'app',

  data() {
    return {
      servers: [],
      loading: false,
      saved: false
    };
  },

  directives: {
    tooltip: Tooltip
  },
  components: {
    StunServer
  },
  methods: {
    removeServer(index) {
      this.servers.splice(index, 1);

      if (this.servers.length === 0) {
        this.addDefaultServer();
      }
    },

    newServer() {
      this.servers.push('');
    },

    addDefaultServer() {
      this.servers.push('stun.nextcloud.com:443');
    },

    debounceUpdateServers: debounce(function () {
      this.updateServers();
    }, 1000),

    async updateServers() {
      this.loading = true; // TODO: your request instead of the timeout

      setTimeout(() => {
        this.loading = false;
        this.toggleSave();
      }, 2000);
    },

    toggleSave() {
      this.saved = true;
      setTimeout(() => {
        this.saved = false;
      }, 3000);
    }

  },

  beforeMount() {
    this.servers = OCP.InitialState.loadState('talk', 'stun_servers');
  }

};